.feedback-form {
  max-width: 480px;
  background-color: #e8f2fb;
  border: 2px solid rgba(9, 9, 9, .937);
  border-radius: 20px;
  flex-direction: column;
  margin-top: 30px;
  margin-left: 30px;
  padding: 12px;
  display: flex;
  box-shadow: -5px -5px 10px #f5f110, 5px 5px 10px #077ff7;
}

.feedback-form label {
  color: #070809;
  text-shadow: 1px 1px 1px #f8efef;
  border-radius: 20px;
  flex-direction: column;
  margin-bottom: 30px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  border-radius: 10px;
  padding: 10px;
}

textarea {
  resize: none;
}

.feedback-form button {
  color: #0b0b09;
  cursor: pointer;
  font: inherit;
  border-radius: 10px;
  padding: 10px 12px;
  box-shadow: -1px -1px 5px #077ff7, 1px 1px 5px #f5f110;
}

